.stats-cointainer {
  padding-top: 0px;
  background-color: #ec6584;
  height: 600px;
  box-sizing: content-box;
}

.stats-counter-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
}

.stat-counter {
  color: white;
  font-size: 2.2rem;
  text-align: center;
  padding: auto;
}

.stat-counter h2,
p {
  padding: 0px;
  margin: 0px;
}

.stats-gallery {
  display: flex;
  flex-direction: column;
}
